<template>
  <div>
     <form id="epay_form" name="auto_submit_form" :action="epayFormData.target" method="post" target="_self">
          <input type="hidden" name="biz_content" :value="epayFormData.bizContent" />
      </form>
  </div>
</template>

<script>
import URL from '@/http/url'
export default {
  data() {
    return {
      epayFormData: {}
    }
  },
  mounted() {
    console.log('iframe page')
    if (this.$util.isIos()) {
      console.log('ios')
      let orderSn = sessionStorage.getItem('orderSn');
      this.$http.post(URL.GET_CHANGE_WEBVIEW_DATA, {
        key: orderSn,
        obj: '',
        method: 'get',
      }).then(res => {
        let result = res.data
        this.epayFormData = result.data
        console.log(this.epayFormData, 'this.epayFormData')
        this.$nextTick(() => {
          document.getElementById('epay_form').submit()
        })
      }).catch(e => {
        console.log('errrrrrrrrr', e)
      })
    } else {
      var epayFormData = sessionStorage.getItem('epayFormData')
      console.log(epayFormData, 'ssssss')
      this.epayFormData = this.$util.strJSONParse(epayFormData, {})
      console.log(this.epayFormData, 'aa')
      this.$nextTick(() => {
        document.getElementById('epay_form').submit()
      })
    }
  }
}
</script>

<style>
div{
  display: none;
}
</style>
